*{
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

#container {
  height: 100vh;
  display: flex;
  background: linear-gradient(to bottom, palegreen, aqua);
}
#drum-machine{
  margin: auto;
  border: 5px solid black;
  height: 620px;
  width: 320px;
  border-radius: 20px;
  background-color: dimgray;
  justify-content: space-between;
}

#drum-keys {
  height: 300px;
  width: 300px;
  display: flex;
  flex-wrap: wrap;
  margin: auto;

}
.drum-pad {
  height: 80px;
  width: 80px;
  border-radius: 10px;
  background-color: black;
  margin: 10px;
  text-align: center;
  font-size: 35px;
  padding-top: 12px;
  color: white;
  display: inline-block;
}
#control{
  height: 300px;
  width: 310px;
  margin: auto;
}
#display {
  border: 2px solid black;
  height: 80px;
  width: 250px;
  margin: 30px auto;
  font-size: xx-large;
  text-align: center;
  padding-top: 10px;
}

#switch {
  border: 1px solid black;
  background-color: black;
  height: 30px;
  width: 80px;
}

#inner-slider{
  border: 1px solid black;
  background-color: white;
  height: 27px;
  width: 40px;
}
#title-power{
  display: flex;
  text-align: center;
}
#power {
  height: 100px;
  width: 150px;
}

#switch-on, #switch-off{
  display: inline;
  margin-right: 25px;
}

#switch-on{
  padding-left: 30px;
}

#switch {
  margin: auto;
}

#range-name-0, #range-name-100{
  display: inline;
  padding-right: 10px;
  padding-left: 10px;
}

#volume-slider {
  margin-left: auto;
  height: 50px;
  width: 300px;
}

#volume-name {
  text-align: center;
}

.range-slider{
  width: 70%
}

@media(min-width: 800px){
  #drum-machine{
    height: 320px;
    width: 650px;
    display: flex;
  }
}
